body {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: rgb(117, 18, 18);
  width: 600px;
}

.App h1 {
  flex: 0 0 100%;
  text-align: center;
  font-size: 4em;
  margin-top: 0;
}
.time-settings {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex: 0 0 50%;
}

.time-settings h2 {
  width: 100%;
  height: 5px;
}

.time-settings #break-length {
  height: 30px;
  width: 30px;
  font-size: 25px;
}

.time-settings #session-length {
  height: 30px;
  width: 30px;
  font-size: 25px;
}

.time-settings button {
  background: none;
  border: none;
  height: 25px;
  font-size: 30px;
}

.time-settings button:active {
  color: grey;
}

.time-settings button:focus {
  outline: none;
}
.display {
  flex: 0 0 350px;
  text-align: center;
  height: 180px;
  border: 15px solid black;
  border-radius: 50px;
}

.display #timer-label {
  font-weight: bold;
  font-size: 50px;
  margin-top: 10px;
}

.display #time-left {
  position: relative;
  font-size: 80px;
  height: 0;
  top: -80px;
}
.controller {
  flex: 0 0 100%;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 10px;
}

.controller i {
  font-size: 40px;
}

.controller #reset {
  margin-left: 30px;
}

.controller span:hover {
  cursor: pointer;
}
